.Persik {
    display: block;
    width: 30%;
    max-width: 240px;
    margin: 15px auto;
}

.Tiles {
    justify-content: space-aroud;
    width: 110px;
    padding-bottom: 3px;
}

.Tiles__content {
    word-break: normal;
    padding: 2px 6px;
    text-align: center;
}

.Tiles__icon {
    display: flex;
    justify-content: center;
    padding: 2px 6px;
}

.Tiles_dop {
    justify-content: space-aroud;
    width: 160px;
    padding-bottom: 3px;
}

.Tiles__content_dop {
    word-break: normal;
    padding: 2px 6px;
    text-align: center;
}

.Tiles__icon_dop {
    display: flex;
    justify-content: center;
    padding: 2px 6px;
}

.balance {
    margin-top: 0!important;
}

.BalanceBox {
    font-family: Roboto, Open Sans, sans-serif;
    font-size: 1vw;
    font-weight: 500;
    color: .999;
    margin-top: 7vw;
    display: flex;
    flex-direction: column;
    align-items: center
}

.BalanceBox .BalanceBox__balance {
    margin-top: .5vw;
    font-weight: 600;
    font-size: 7vw;
    display: flex;
    align-items: center;
    text-align: center;
    max-width: 90vw
}

@media (-webkit-min-device-pixel-ratio:2),
(min-resolution:192dpi) {
    .MainPage__clickcoin-inner {}
}

@media screen and(max-height:560px) {
    .MainPage__clickcoin-inner {
        max-height: 120px;
        bottom: 0;
        top: auto
    }
    .MainPage__clickcoin-inner .MainPage__online {
        bottom: 40px
    }
}

.MainPage__clickcoin-inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 90%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-align-items: flex-end;
    align-items: flex-endж
}

.MainPage__clickcoin {
    height: 0;
    overflow: hidden;
    padding-top: 56.26%;
    position: relative;
    outline: none;
    max-width: 650px;
    z-index: 2;
    bottom: 0px;
    margin-left: auto;
    margin-right: auto;
}

.balance {
    margin-top: 0!important;
}

.ratingMyCell .Cell__in {
    background: var(--events_card_color_bg);
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
    margin-top: 5px;
}

.ratingPosition {
    color: #4b91ff;
    min-width: 22px;
    padding-right: 5px;
    font-size: 12pt;
    text-align: left;
    font-weight: bold;
}

.ratingAvatar .Avatar__children {
    background: #2196F3;
    background: linear-gradient(65deg, #3F51B5, #03A9F4);
}

.ratingAvatar.my .Avatar__in {
    border-radius: 50%;
    box-shadow: 0 0 0 3px #2196F3;
}

#rating .List {
    padding-top: 10px;
}

.table {
    margin-right: 12px;
}

.balance h1 {
    font-weight: 700;
    margin: 0;
    font-size: 35px;
    text-align: center;
}

.balance p {
    margin: 0;
    text-align: center;
    opacity: .7;
}

@media (-webkit-min-device-pixel-ratio:2),
(min-resolution:192dpi) {
    .MainPage__clickcoin-inner {}
}

.safe:active {
    transform: scale(1.05);
}

.safe {
    width: 80vw;
    transition: all .02s ease-in-out;
    height: unset!important;
}

.safe .text2,
.safe text {
    font-weight: 600;
    text-align: center;
}

.safe text {
    font-size: 38px;
    fill: #233156;
    letter-spacing: 19px;
}

